.loader {
  height: 12px;
  width: 80%;
  border: 1px solid #fff;
  border-radius: 10px;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  height: 100%;
  width: 0;
  background: #5b5151;
  animation: prog 6s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes prog {
  to {
    width: 100%;
  }
}

.cat-img {
  width: 460px;
}

.cat-info {
  display: flex;
}

.cat-text {
  display: block;
}

.breed-select {
  width: 460px;
}

